import React from 'react';
import BackArrow from './BackArrow';

export default function About(props){
  return(
    <body>
      <div className={'container'} onClick={() => props.loadedPage('CardDisplay')}>
        <BackArrow loadedPage={props.loadedPage}/>
        <h1>About Me</h1>
      </div>

      <div id={'hometown'} className={'content-bg'}>
        <h1>Hometown</h1>
        <p>Born in Grand Rapids, Michigan, on March 19th, 2002.</p>
        <p>Raised 20 minutes north in the town of Rockford, Michigan.</p>
        <p>Now will be spending a majority of my time in Mount Pleasant, Michigan, where I attend Central Michigan University</p>
      </div>

      <div id={'education'} className={'content-bg'}>
        <h1>Education</h1>
        <p>Spent all of my K-12 years in the Rockford Public School system.</p>
        <p>Graduated from Rockord High School in July of 2020</p>
        <p>Now enrolled at Central Micigan University with a major in Computer Science. Will be a Sophomore as of fall 2021</p>
      </div>

      <div id={'hobbies'} className={'content-bg hobbies'}>
        <h1>Hobbies</h1>
        <p>I ran Cross Country and Track 6th-12th grade. My running days are long behind me now, however.</p>
        <p>I love worship music. Praising God through song is one of the ways that I feel most connected to Him.</p>
        <p>I love basketball. If I have free time, odds are I'm either playing or watching.</p>
      </div>

    <div></div>

    </body>
  )
}
