import React from 'react';

export default function CardDisplay(props){
  return(
    <>
      <div className={'container'}>
        <h1>Ian Cook</h1>
      </div>

      <div className={'card_container'}>
          <div className={'card'} onClick={() => props.loadedPage('About')}>
            <div className={'card_header'}>
              <h1>About Me</h1>
            </div>
          </div>



          <div className={'card'} onClick={() => props.loadedPage('Studies')}>
            <div className={'card_header'}>
              <h1>Good News</h1>
            </div>
          </div>



        <div className={'card'} onClick={() => props.loadedPage('Contact')}>
          <div className={'card_header'}>
            <h1>Contact Info</h1>
          </div>
        </div>


      </div>

    </>
  );
}
