import React, {useState, useEffect} from 'react';
import CardDisplay from './CardDisplay';
import Contact from './Contact';
import Studies from './Studies';
import About from './About';

export default function Homepage() {

  const [loadedPage, setLoadedPage] = useState('CardDisplay');

  return (
    <>
      {
        loadedPage === 'CardDisplay' ?
        <CardDisplay loadedPage={setLoadedPage}/>
        :
        loadedPage === 'Contact' ?
        <Contact loadedPage={setLoadedPage}/>
        :
        loadedPage === 'About' ?
        <About loadedPage={setLoadedPage}/>
        :
        <Studies loadedPage={setLoadedPage}/>
      }
    </>
  );
}
