import React from 'react';
import BackArrow from './BackArrow';

export default function Studies(props){
  return(
    <body>
      <div className={'container'}>
        <BackArrow loadedPage={props.loadedPage}/>
        <h1>Spreading the Good News</h1>
      </div>


      <div className={'content-bg'}>
        <h1>The Gospel in 4 parts</h1>
      </div>

      <div className={'content-bg'}>
          <h1>The Plan</h1>
          <p>God creates the world and everything in it, and He sees that it is good. (Genesis 1:4, 10, 12, 18, 21, 25, 31)</p>
          <p>God longs to have a relationship with us.</p>
      </div>

      <div className={'content-bg'}>
          <h1>The Problem</h1>
          <p>The Fall: Adam and Eve sin (Genesis 3)</p>
          <p>All humans since</p>
      </div>

      <div className={'content-bg'}>
          <h1>The Solution</h1>
      </div>

      <div className={'content-bg'}>
          <h1>The Response</h1>
      </div>

    </body>
  )
}
